@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 300;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    font-size: 16px;
}

#root {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.roboto-light {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
}

.roboto-regular {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

.roboto-black {
    font-family: "Roboto", sans-serif;
    font-weight: 900;
}

.color-red {
    color: #D42026;
}

.background-red {
    background-color: #D42026;
}

.color-black {
    color: #4e4e4e;
}

.color-white {
    color: #ffffff;
}

::-webkit-scrollbar-track {
  background-color: #fbe9e9;
}
::-webkit-scrollbar {
  width: 6px;
  background: #fbe9e9;
}
::-webkit-scrollbar-thumb {
  background: #D42026;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #D42026;
  border-color: #D42026 transparent #D42026 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.search__input {
    display: block;
    background-clip: padding-box;
    border-radius: .25rem;
    overflow: visible;
}

.search__input:focus {
    outline: none;
}

.table {
    border-collapse: collapse;
    width: 100%;
}

.table tr {
    display: table-row;
    border-color: inherit;
}

.table th {
    border-right: 10px solid #fff;
}

.table th:last-child {
    border-right: none;
}

.table th {
    text-align: left;
    width: 25%;
}

.table td:last-child, th:last-child {
    width: 50%;
}

.table tbody tr:hover {
    cursor: pointer;
    background-color: #fbe9e9;
}

.paginator div:nth-last-child(2) {
    margin-right: 0 !important;
}

.search-page {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.details-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  max-height: 86vh;
  overflow-y: auto;
}

.details-page__grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 2rem;
  grid-row-gap: 1rem;
  grid-template-areas:
  "image text text"
  "image events events"
  "image series series"
  "image stories stories";
}

.details-page__mobile{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.image {
  grid-area: image;
}

.text {
  grid-area: text;
}

.events{
  grid-area: events;
}

.series{
  grid-area: series;
}

.stories{
  grid-area: stories;
}

ul {
  list-style-type: none;
  margin-left: 1.5rem;
}

